<template lang="pug">
  .pg
    .pg__row
      .pg__row__section
        .top-panel.darker
        .pg__row__section__title Sandbox
        .pg__row__section__description
          span Use the sandbox to hone your skills in PostgreSQL. This is a non-destructive sandbox, experiment as much as you want, however you want. Practice your queries to prepare for the sessions or try your hand at a tournament.
        .pg__row__section__body
          .pg__row__section__body__item(v-for="item in databases")
            .pg__row__section__body__item__left-panel
            .pg__row__section__body__item__title {{item.name}}
            .pg__row__section__body__item__tables {{item.count}} Tables
      .pg__row__section
        .top-panel.darker
        .pg__row__section__title Tournament
        .pg__row__section__description
          span The tournament is your place to prove your skills and make real money. Hone your skills in the Sandbox and Sessions to prepare for the timed Tournament. You’ll have access to a UML diagram shortly beforehand to study. Be ready.
        .pg__row__section__tournament(style="font-size:16px;") Next Tournament
          br
          span(style="font-size:18px;") TBD
    .pg__row
      .pg__row__section.full
        .top-panel.darker
        .pg__row__section__title Exercises
        .pg__row__section__description
          span Exercises are provided for you to incrementally increase and perfect your skills. Exercises grow in complexity as you go through them, boosting your programming speed and knowledge quickly. Go through these to build your confidence for the Tournaments to prove your worth.
        .pg__row__section__category Novice
        .pg__row__section__content
          .pg__row__section__content__exercise(v-for="item in [1,2,3]") hey
            .number-bg
            .number {{item}}
        .pg__row__section__category Intermediate
        .pg__row__section__content
          span hey
        .pg__row__section__category Advanced
        .pg__row__section__content
          span hey
</template>

<script>
  export default {
    data () {
      return {
        databases: [
          {count: 12, name: 'Libra University'},
          {count: 8, name: 'Universal Research'},
          {count: 4, name: 'Common Grounds'},
          {count: 10, name: 'Liberty Gray'},
          {count: 9, name: 'Food Xchange'},
          {count: 21, name: 'Animals For All'}
        ]
      }
    }
  }
</script>

<style lang="sass" scoped>
  @import '../assets/sass/vars'
  .pg
    padding-top: 410px
    margin: 0px auto
    width: 1040px
    &__row
      width: 100%
      height: auto
      display: flex
      justify-content: space-between
      margin-bottom: 60px
      &__section
        /*border: 1px red solid*/
        width: 495px
        /*height: 300px*/
        position: relative
        &.full
          width: 100%
        &__title
          +special-elite-font()
          font-size: 28px
          color: #303030
          padding-top: 13px
        &__description
          +averia-font()
          color: #3f3f3f
          font-size: 13px
          margin-bottom: 20px
        &__category
          +averia-font()
          font-size: 17px
          color: #3f3f3f
          margin-bottom: 8px
        &__content
          display: flex
          justify-content: space-between
          &__exercise
            width: 315px
            height: 132px
            border: 1px #666767 solid
            background-color: #e8e8e8
            border-radius: 3px
            overflow: hidden
            position: relative
            .number-bg
              width: 100px
              height: 100px
              background-color: #646464
              position: absolute
              top: -64px
              left: -64px
              transform: rotateZ(45deg)
            .number
              position: absolute
              top: 3px
              left: 8px
              color: #e8e8e8
              +averia-font()
              font-size: 15px
        &__body
          margin-top: 36px
          /*height: 100px*/
          /*border: 1px blue solid*/
          display: flex
          flex-flow: row wrap
          justify-content: space-between
          &__item
            position: relative
            width: 154px
            height: 46px
            border: 1px #878787 solid
            background-color: #dedede
            border-radius: 3px
            margin-bottom: 15px
            overflow: hidden
            cursor: pointer
            &:hover
                background-color: rgba(83, 110, 123, 0.2)
            &__left-panel
              width: 4px
              height: 100%
              position: absolute
              left: 0
              top: 0
              background-color: #536e7b
            &__title
              position: absolute
              top: 7px
              left: 9px
              +averia-font()
              font-size: 15px
              color: #3f3f3f
            &__tables
              position: absolute
              top: 25px
              left: 9px
              +averia-font()
              font-size: 14px
              color: rgba(63, 63, 63, 0.7)
        &__tournament
          width: 300px
          height: 65px
          position: relative
          margin: 55px auto 0
          border: 2px #7f6932 solid
          border-radius: 4px
          background-color: rgba(127, 105, 50, 0.14)
          +averia-font()
          color: #292929
          text-align: center
          padding-top: 12px
          box-sizing: border-box
          cursor: pointer
          &:hover
            background-color: rgba(127, 105, 50, 0.20)

</style>



