<template lang="pug">
  .titles
    .titles__con(:class="`state-${layoutState}`")
        .titles__con__main-title #DevTournament
        .titles__con__sub-title Competition that matters.
        .titles__con__subject(v-if=`layoutState !== 0`)
          .titles__con__subject__pg(v-if=`subject === 'postgres'`)
            span.titles__con__subject__pg__icon
              img(src=`../assets/img/PageTitles/postgres_icon_uncompressed.png`)
            span.titles__con__subject__pg__text &nbsp;POSTGRESQL
</template>

<script>
  import {mapState} from 'vuex'

  export default {
    computed: {
      ...mapState({
        layoutState: state => state.app.layoutState,
        subject: state => state.app.subject
      })
    }
  }
</script>

<style lang="sass">
  @import '../assets/sass/vars'

  .titles
    position: absolute
    top: 0
    width: 100%
    /*border: 1px gold solid*/
    box-sizing: border-box
    /*color: red*/
    &__con
      /*border: 1px blue solid*/
      position: relative
      max-width: 400px
      margin: 0px auto
      text-align: center
      letter-spacing: .05em
      height: 30px
      transition: all 170ms linear
      &__main-title
        +special-elite-font()
        color: #fdfcf9
        font-size: 36px
      &__sub-title
        margin-top: 14px
        +averia-font()
        font-size: 18px
        color: #e9e9e9
      &__subject
        position: relative
        top: 90px
        img
          position: relative
          top: 12px
        +averia-font()
        font-size: 36px
        color: #e9e9e9
      &.state-0
        top: 180px
      &.state-1
        top: 112px
</style>



