<template lang="pug">
  modal-window(v-if="show")
    .content(v-if="!loggedIn")
      template(v-if="loginState")
        .headings
          .headings__title LOGIN
          //.headings__sub-title Enter email to begin.
        .form
          input-field(
            id="login-email-input",
            name="email",
            @update="updateLogin('username', $event)",
            @enter="sendLogin"
          )
          input-field(
            id="login-password-input",
            name="password",
            :password="true",
            @update="updateLogin('password', $event)",
            @enter="sendLogin"
          )
          .headings
            .headings__sub-title.select(style="margin-top: 35px;", @click=`sendLogin`) Login
          //img(src="../assets/img/LoginRegister/right-arrow.png", class="login-arrow", @click="sendLogin(user)")
        .headings(style="margin-top: 50px;")
          .headings__title REGISTER
          //.headings__sub-title Click to register a new account.
          .headings__sub-title.select(style="margin-top: 30px;", @click="toggleLoginState") Register for a new account
      template(v-else)
        .headings
          .headings__title REGISTER
        .form
          input-field(
            id="register-email-input",
            name="email",
            @update=`updateRegister('email', $event)`,
          )
          input-field(
            id="register-username-input",
            name="username",
            @update=`updateRegister('username', $event)`,
          )
          input-field(
            style="margin-top:50px",
            id="register-password1-input",
            name="password",
            :password="true",
            @update=`updateRegister('password1', $event)`,
          )
          input-field(
            id="register-password2-input",
            name="confirm password",
            :password="true",
            @update=`updateRegister('password2', $event)`,
          )
          .headings
            .headings__sub-title.select(style="margin-top: 50px;") Register
    .content(v-else)
      .headings
        .headings__title LOGOUT
        .headings__sub-title End your session
      button(@click="logout") Logout

</template>

<script>
  import ModalWindow from '@/blocks/ModalWindow'
  import InputField from '@/blocks/InputField'
  import {mapActions, mapState} from 'vuex'

  export default {
    components: {
      ModalWindow,
      InputField
    },
    props: {
      show: {required: true}
    },
    data () {
      return {
        returningUser: {
          username: '',
          password: ''
        },
        newUser: {
          email: '',
          username: '',
          password1: '',
          password2: ''
        },
        loginState: true // determining whether to show the login screen or registration screen
      }
    },
    computed: {
      ...mapState({
        loggedIn: state => state.user.loggedIn
      })
    },
    methods: {
      toggleLoginState () {
        this.loginState = !this.loginState
      },
      async sendLogin () {
        const success = await this.login(this.returningUser)
        if (success) {
          console.log('TODO: make success message')
          this.$emit('toggle-auth')
        } else {
          console.log('TODO: in component and wrong credentials')
        }
      },
      ...mapActions({
        login: 'user/login',
        testLogin: 'user/testLogin',
        logout: 'user/logout'
      }),
      updateLogin (...args) {
        // args[0] is the field name (eg. username)
        // args[1] is the actual value from the field
        this.$set(this.returningUser, args[0], args[1])
      },
      updateRegister (...args) {
        this.$set(this.newUser, args[0], args[1])
      }
    }
  }
</script>

<style lang="sass">
  @import '../assets/sass/vars'

  .headings
    margin-bottom: 19px
    +averia-font()
    .headings__title
      +text-normal-white(1.0)
      font-size: 20px
      margin-bottom: 5px
    .headings__sub-title
      +text-normal-white(0.8)
      font-size: 12px
      color: $light-border
      &.select
        border: 1px $light-border solid
        padding: 8px
        max-width: 160px
        text-align: center
        cursor: pointer
        &:hover
          background-color: transparentize($light-border, 0.85)
  .form
    .login-arrow
      margin-top: 20px
      cursor: pointer
      opacity: 0.85
      transition: opacity 30ms linear
      &:hover
        opacity: 1
        transition: opacity 30ms linear

</style>



