<template lang="pug">
  .modal
    .top-panel
    .bottom-panel
    slot
</template>

<script>
  export default {
  }
</script>

<style lang="sass" scoped>
  @import '../assets/sass/vars'

  .modal
    position: fixed
    height: 400px
    width: 600px
    left: 50%
    top: 50%
    padding: 35px
    background-color: rgba(38,38,38,0.98)
    transform: translateX(-50%) translateY(-50%)
    border-radius: $border-panel-radius
</style>



