<template lang="pug">
  #app
    .bg(:class="{select: layoutState === 1}")
    app-nav(@toggle-auth="toggleAuth")
    router-view
    page-titles
    login-register(:show="authVisibility", @toggle-auth="toggleAuth")
</template>

<script>
  import {mapState, mapGetters, mapActions, mapMutations} from 'vuex'
  import AppNav from '@/components/AppNav'
  import LoginRegister from '@/components/LoginRegister'
  import PageTitles from '@/components/PageTitles'

  export default {
    name: 'app',
    components: {
      AppNav,
      LoginRegister,
      PageTitles
    },
    data () {
      return {
        authVisibility: false
      }
    },
    computed: {
      ...mapState({
        layoutState: state => state.app.layoutState
      }),
      ...mapGetters({
        fullName: 'fullName'
      })
    },
    methods: {
      toggleAuth () {
        this.authVisibility = !this.authVisibility
      },
      ...mapActions({
        getName: 'getName'
      }),
      ...mapMutations({
        setKey: 'setKey'
      })
    },
    created () {
      // FIXME: this goes somewhere else
      // const loginCookieSet = this.$cookies.isKey('tokenKey')
      // console.log(loginCookieSet)
      // if (loginCookieSet) {
      //   this.setKey({key: this.$cookies.get('tokenKey')})
      //   this.getName()
      // }
    }
  }
</script>

<style lang="sass">
  @import 'assets/sass/app'

  #app
    min-height: 100%

  li
    color: red
</style>



